import { google } from 'googleapis';
import { z } from 'zod';

const signupSchema = z.object({
  userName: z.string(),
  email: z.string().email(),
  password: z.string(),
});

const loginSchema = z.object({
  userName: z.string(),
  password: z.string(),
});

export type SignupData = z.infer<typeof signupSchema>;
export type LoginData = z.infer<typeof loginSchema>;


const {
  GOOGLE_SHEETS_CLIENT_EMAIL,
  GOOGLE_SHEETS_PRIVATE_KEY,
  GOOGLE_SHEETS_SHEET_ID,
} = process.env;

if (!GOOGLE_SHEETS_CLIENT_EMAIL || !GOOGLE_SHEETS_PRIVATE_KEY || !GOOGLE_SHEETS_SHEET_ID) {
  throw new Error('Google Sheets API credentials are not configured in .env');
}

const auth = new google.auth.GoogleAuth({
  credentials: {
    client_email: GOOGLE_SHEETS_CLIENT_EMAIL,
    private_key: GOOGLE_SHEETS_PRIVATE_KEY.replace(/\\n/g, '\n'),
  },
  scopes: ['https://www.googleapis.com/auth/spreadsheets'],
});

const sheets = google.sheets({ version: 'v4', auth });
const SPREADSHEET_ID = GOOGLE_SHEETS_SHEET_ID;
const SHEET_NAME = 'credentials';

export async function appendUserToSheet(userData: SignupData) {
  const { email, userName, password } = userData;
  const timestamp = new Date().toISOString();

  // Mapping to sheet headers: emailAddress, userName, pwd, databaseID, folderID, status, lastLogin, runtime
  const values = [[email, userName, password, '', '', 'active', timestamp, '']];

  const request = {
    spreadsheetId: SPREADSHEET_ID,
    range: `${SHEET_NAME}!A1`, // Appends to the first empty row of the sheet
    valueInputOption: 'USER_ENTERED',
    resource: {
      values,
    },
  };

  try {
    const response = await sheets.spreadsheets.values.append(request);
    console.log('Appended to sheet:', response.data);
    return response.data;
  } catch (err) {
    console.error('The API returned an error: ' + err);
    throw new Error('Failed to append data to Google Sheet.');
  }
}

export async function getAllUsers() {
    try {
        const response = await sheets.spreadsheets.values.get({
            spreadsheetId: SPREADSHEET_ID,
            range: SHEET_NAME,
        });

        const rows = response.data.values;
        if (!rows || rows.length === 0) {
            return [];
        }

        const header = rows[0];
        const emailIndex = header.indexOf('emailAddress');
        const userNameIndex = header.indexOf('userName');
        const pwdIndex = header.indexOf('pwd');

        if (userNameIndex === -1 || pwdIndex === -1) {
            throw new Error('Could not find required columns (userName, pwd) in the sheet.');
        }

        return rows.slice(1).map(row => ({
            emailAddress: row[emailIndex],
            userName: row[userNameIndex],
            pwd: row[pwdIndex],
        }));

    } catch (err) {
        console.error('The API returned an error: ' + err);
        throw new Error('Failed to retrieve data from Google Sheet.');
    }
}
